import React from 'react'
import { Link } from 'react-router-dom'
import MetaData from './MetaData'

const NotFound = () => {
  return (
    <><MetaData title={'Not Found - JimBook'}/>
    <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
      <i className=" text-white text-7xl fas fa-dumbbell mb-4"></i>
    <h1 className="text-6xl font-bold">404</h1>
    <p className="text-2xl font-medium my-4">Page Not Found</p>
    <Link to='/' className="bg-orange-500 hover:bg-orange-700 text-white font-bold py-2 px-4 rounded-full">
      Go Back Home
    </Link>
  </div>
  </>
  )
}

export default NotFound