import React, { useState } from "react";
import { Dialog, DialogActions , DialogContent  , Button  ,createTheme, ThemeProvider} from '@material-ui/core'
import { Link } from "react-router-dom";



const Footer = () => {

    const theme = createTheme({
        palette: {
          type: 'dark',
        },
      });

    const [open, setOpen] = useState(false)

  const submitReviewToggle = () => {
    open ? setOpen(false) : setOpen(true)
  }


  return (
    <>
      <footer className="bg-gray-900 text-white">
        <div className="max-w-7xl mx-auto py-8 px-4 sm:px-6 lg:px-8 grid grid-cols-1 sm:grid-cols-2 gap-4">
          <div className="mb-8">
            <h3 className="text-lg font-medium mb-4">About Us</h3>
            <ul className="space-y-4">
              <li>
                <button onClick={submitReviewToggle}  className="hover:text-gray-400">
                  How calculator works??
                </button>
              </li>
              <li>
                <Link to="/shop" className="hover:text-gray-400">
                 Our Shop
                </Link>
              </li>
              <li>
                <Link to='/contact' className="hover:text-gray-400">
                  Contact Us
                </Link>
              </li>
            </ul>
          </div>
          <div>
            <h3 className="text-lg font-medium mb-4">Connect with Us</h3>
            <div className="flex space-x-4">
              <a rel="noreferrer" target={'_blank'}  href="https://www.linkedin.com/in/tarun-devgan-0b8013235/"  className=" text-xl hover:text-gray-400">
              <i class="fab fa-linkedin"></i>
              </a>
              <a rel="noreferrer" target={'_blank'}  href="https://www.instagram.com/tarun_devgnn/?next=%2F" className=" text-xl hover:text-gray-400">
              <i class="fab fa-instagram"></i>
              </a>
            </div>
          </div>
        </div>
        <div className="bg-gray-800 py-4">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex justify-between items-center">
            <p className="text-sm text-gray-400">
              &copy; 2023 JimBook  All rights reserved.
            </p>
            <p className="text-sm text-gray-400">
              Designed and developed by 
              <a  rel="noreferrer" target={'_blank'} href="https://www.linkedin.com/in/tarun-devgan-0b8013235/" className="underline ml-1 hover:text-gray-300">
              Tarun Devgan
              </a>
            </p>
          </div>
        </div>
      </footer>
      <ThemeProvider theme={theme}>

      <Dialog
        aria-labelledby="simple-dialog-title"
        open={open}
        onClose={submitReviewToggle} 
        >
          <h3 className="text-sky-500 my-4 px-6 text-2xl ">Note</h3>
          <DialogContent className="submitReviewDialog">
                <p className="text-sm text-white"> In the name of product there is quantity stored. Like in "Peanut buttter (1 tbsp)" It shows when you add 1 quantity, then its 1 tbps and when you add 2 quantity, then its 2 tbps. Note That our data is not accurate!! So, do make better analysis!!</p>
          </DialogContent>
          <DialogActions>
            <Button onClick={submitReviewToggle} color='secondary' >Close</Button>
          </DialogActions>
        </Dialog>
        </ThemeProvider>

    </>
  );
};

export default Footer;
