import React, {  useState } from 'react';
import { useLocation, Link, useHistory } from 'react-router-dom';
 import { ToastContainer, toast } from 'react-toastify';
  import 'react-toastify/dist/ReactToastify.css';

function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const location = useLocation();
  const history = useHistory();

  const handleLogout = () => {
    localStorage.removeItem('token');
    history.push('./login');
    toast.success("Logout Successfully")
  };

 
  return (
    <>
    <ToastContainer
position="bottom-center"
autoClose={3000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover
theme="dark"
/>
      <nav className=" fixed top-0 left-0 right-0 bg-gray-900 shadow-lg z-10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16">
            <div className="flex">
              <Link to="/" className="flex-shrink-0 flex items-center">
     
                <span className="ml-2 text-white text-2xl"> <i className=" text-white  fas fa-dumbbell mr-1"></i> Jim Book </span>
              </Link>
            </div>
            <div className="hidden md:flex   items-center ">
              <div className="flex items-center ">
                <Link
                  to="/addNote"
                  className={`${
                    location.pathname === '/addNote' ? 'text-white' : 'text-gray-300'
                  } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
                >
                  Note Calories
                </Link>
                <Link
                  to="/smoothieCalculator"
                  className={`${
                    location.pathname === '/smoothieCalculator'
                      ? 'text-white'
                      : 'text-gray-300'
                  } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
                >
                  Smoothie Calculator
                </Link>
                <Link
                  to="/shop"
                  className={`${
                    location.pathname === '/shop'
                      ? 'text-white'
                      : 'text-gray-300'
                  } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
                >
                  Our Shop
                </Link>
                <Link
                  to="/contact"
                  className={`${
                    location.pathname === '/contact'
                      ? 'text-white'
                      : 'text-gray-300'
                  } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
                >
                  Contact
                </Link>

              </div>
            </div>
           
            <div className="md:hidden flex items-center mr-0">
              <button
                type="button"
                className="text-gray-500 hover:text-white focus:outline-none focus:text-white"
                onClick={() => setIsOpen(!isOpen)}
              >
                <svg
                  className="h-6 w-6 fill-current"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  {isOpen ? (
                      <path fillRule="evenodd" clipRule="evenodd" d="M12.7071 7.29289C12.3166 6.90237 11.6834 6.90237 11.2929 7.29289L7.29289 11.2929C6.90237 11.6834 6.90237 12.3166 7.29289 12.7071C7.68342 13.0976 8.31658 13.0976 8.70711 12.7071L12 9.41421L15.2929 12.7071C15.6834 13.0976 16.3166 13.0976 16.7071 12.7071C17.0976 12.3166 17.0976 11.6834 16.7071 11.2929L12.7071 7.29289Z" fill="white"/>

                    ) : (
                    <path fillRule="evenodd" clipRule="evenodd" d="M11.2929 16.7071C11.6834 17.0976 12.3166 17.0976 12.7071 16.7071L16.7071 12.7071C17.0976 12.3166 17.0976 11.6834 16.7071 11.2929C16.3166 10.9024 15.6834 10.9024 15.2929 11.2929L12 14.5858L8.70711 11.2929C8.31658 10.9024 7.68342 10.9024 7.29289 11.2929C6.90237 11.6834 6.90237 12.3166 7.29289 12.7071L11.2929 16.7071Z" fill="white"/>
                      
                    )}
                  </svg>
                </button>
                
              </div>

              {!localStorage.getItem('token') ? (
                  <Link
                    to="/login"
                    className="bg-orange-500 text-white hover:text-gray-700 px-3 my-3 rounded-md text-sm font-medium  flex items-center"
                  >
                    Login
                  </Link>
  
              ) : (
                <button
                  onClick={handleLogout}
                  className="bg-red-500 text-white hover:text-gray-700 px-3 my-2 rounded-md text-sm font-medium ml-4"
                >
                  Logout{' '}
                </button>
              )}
            </div>
          </div>
      

                      {/* small window responsive  */}
          <div className={`${isOpen ? 'flex items-center ' : 'hidden'} md:hidden`}>
            <div className=" flex flex-col justify-center px-2 pt-2 pb-3 space-y-1 sm:px-3 overflow-x-auto whitespace-nowrap ">
              <Link
                to="/addNote"
                className={`${
                  location.pathname === '/addNote'
                    ? 'text-white'
                    : 'text-gray-300'
                } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
              >
                Note Calories
              </Link>
              <Link
                  to="/shop"
                  className={`${
                    location.pathname === '/shop'
                      ? 'text-white'
                      : 'text-gray-300'
                  } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
                >
                  Our Shop
                </Link>
              <Link
                to="/smoothieCalculator"
                className={`${
                  location.pathname === '/smoothieCalculator'
                    ? 'text-white'
                    : 'text-gray-300'
                } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
              >
                Smoothie 
              </Link>
              <Link
                to="/contact"
                className={`${
                  location.pathname === '/contact'
                    ? 'text-white'
                    : 'text-gray-300'
                } hover:text-white px-3 py-2 rounded-md text-sm font-medium`}
              >
                Contact
              </Link>
            
            </div>
          </div>
        </nav>
      </>
);
}

export default Navbar;



      
