import React from 'react'
import nutrela from './Images/nutrela.png'
import oats from './Images/oats.png'
import whey from './Images/whey.png'
import MetaData from './MetaData'


const Shop = () => {
  const productData = [
  {
    id: 1,
    name: "Nutella Hazelnut Spread with Cocoa",
    description: "Serving per container , 200 Calories , 2g protein",
    price: "250-350",
    image: nutrela,
    link : 'https://amzn.to/425qvZa'
  },
  {
    id: 2,
    name: "Quaker Old Fashioned Rolled Oats",
    description: "Non GMO Project Verified, Two 64oz Bags in Box, 90 Servings, 4 Pound (Pack of 2)",
    price: "1421",
     image: oats,
    link : 'https://www.amazon.com/Quaker-Fashioned-Oatmeal-Breakfast-Cereal/dp/B01KV4H51G/ref=mp_s_a_1_3?keywords=oats&sr=8-3'
  },
  {
    id: 3,
    name: "Body Fortress Super Advanced Whey Protein Powder,",
    description: "1.78 lb container of Body Fortress Chocolate Super Advanced Whey Protein Powder",
    price: "900 - 1400",
    image: whey,
    link : "https://amzn.to/3JBokWa"
  },
];

  return (
    <> <MetaData title={'Shop - JimBook'}/>
    <div className='min-h-screen bg-gradient-to-br from-gray-900 to-black py-8 '>
        <h2 className='text-white py-8 mt-5 text-center text-3xl'>Shop</h2>
          <div className="flex flex-wrap justify-center  text-white">
      {productData.map(product => (
        <div key={product.id} className="max-w-xs w-full mx-2 my-4 bg-gray-900 rounded-lg overflow-hidden shadow-md hover:shadow-lg hover:bg-black cursor-pointer">
          <div style={{overflow : 'hidden' , display : 'flex' , alignItems : 'center' , justifyContent :"center" }} className='w-full h-64'>
          <img style={{objectFit  : "cover" , aspectRatio : 2/3 , width : '40%' }} src={product.image} alt={product.name} className=" object-cover" />
          </div>
          <div className="px-4 py-2">
            <h3 className="text-gray-200 font-semibold my-2">{product.name}</h3>
            <p className="text-gray-400 my-2">{product.description}</p>
            <p className="text-gray-200 font-semibold text-green-500 my-3">₹ {product.price}</p>
          </div>
          <div className="flex justify-center pb-4">
            <a href={product.link} rel="noreferrer" target={'_blank'}   className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full">
              Buy Now
            </a>
          </div>
        </div>
      ))}
    </div>
</div>
</>
  )
}

export default Shop