import React, { useContext, useEffect , useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import noteContext from "../context/notes/noteContext";
import AddNote from "./AddNote";
import NoteItem from "./NoteItem";

export default function Notes(props) {
  const context = useContext(noteContext);
  const { notes, editNote , getNotes } = context;

  let history = useHistory();
  useEffect(() => {
    if(localStorage.getItem("token"))
    {
      getNotes();
    }
    else {
      history.push("./login")
    }
    // eslint-disable-next-line
  }, []);

  const ref = useRef(null)
  const refClose = useRef(null)
  const [note, setNote] = useState({ id : "" , etitle: "", edescription: "", etag: ""})

  const updateNote = (currentNote) => {
      ref.current.click();
      setNote({id : currentNote._id ,  etitle: currentNote.title, edescription: currentNote.description, etag:currentNote.tag})
  }

  const handleClick = ()=>{
      // console.log("Updating the note...", note)
      editNote(note.id , note.etitle , note.edescription);
      refClose.current.click();
  }

  const onChange = (e)=>{
      setNote({...note, [e.target.name]: e.target.value})
  }
  return (
    <div className="container ">
      <AddNote />
      
      <button
      ref={ref}
        type="button"
        className="btn btn-primary "
        style={{display : "none"}}
        data-bs-toggle="modal"
        data-bs-target="#exampleModal"
      >
        Launch demo modal
      </button>

<div className="modal fade " id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog   py-4 border-none">
    <div className="modal-content  p-4 bg-gradient-to-br from-gray-900 to-gray-800  text-gray-100">
      <div className="pt-7">
        <h1 className="modal-title text-center text-2xl text-sky-500" id="exampleModalLabel">
         Update Note
        </h1>
        <button
          type="button"
          className="btn-close"
          data-bs-dismiss="modal"
          aria-label="Close"
        ></button>
      </div>
      <div className="modal-body">
        <form>
          <div className="mb-3">
            <label htmlFor="exampleInputEmail1" className="text-xl py-3 text-orange-400">Title</label>
            <input type="text" className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-400 rounded-t-md focus:outline-none focus:ring-primary-500 focus:border-primary-500 focus:z-10 sm:text-sm bg-gray-900 text-white-100 " minLength={5} required value={note.etitle} id="etitle" name='etitle' aria-describedby="emailHelp " onChange={onChange}/>
          </div>
          <div className="mb-3">
            <label htmlFor="exampleInputPassword1" className="text-xl py-3 text-orange-400">Description</label>
            <textarea rows={4} name='edescription' minLength={5} required value={note.edescription} className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-400 placeholder-gray-500 rounded-t-md focus:outline-none focus:ring-primary-500 focus:border-primary-500 focus:z-10 sm:text-sm bg-gray-900 text-white-100" id="edescription" onChange={onChange}/>
          </div>
        </form>
      </div>
      <div className="py-4 flex justify-end px-4 ">
        <button
          type="button"
          className="btn bg-red-500 hover:bg-red-600 text-white mx-4 px-3"
          data-bs-dismiss="modal"
          ref={refClose}
        >
          Close
        </button>
        <button disabled={note.etitle.length<5 || note.edescription.length<5} onClick={handleClick} type="button" className="btn btn-primary bg-sky-500">
          Update Note
        </button>
      </div>
    </div>
  </div>
</div>


      <h2 className="text-3xl py-3 text-center text-green-500  "> Your Notes</h2>
      <div className=" row">
        {notes.length === 0 && "No Notes added"}
        {notes.map((note) => (
          <NoteItem note={note} key={note._id} updateNote={updateNote} />
        ))}
      </div>
    </div>
  );
}
