import React, {useContext , useState} from 'react'
import noteContext from '../context/notes/noteContext'



const AddNote = () => { 
    const context = useContext(noteContext)
    const { addNote} = context;
    const [note, setNote] = useState({title : "" , description : "" , tag : "default"});

    const handleSubmit = (e) => {
        e.preventDefault()
        addNote(note.title , note.description , note.tag)
        setNote({title : "" , description : "" , tag : ""})
        
    }

    const onChange = (e) => {
        setNote({...note , [e.target.name]: e.target.value })  // Jo bhi name hai isda e.target.name ohh iss di value de barabar hojaye e.target.value jo assi likhya hai
    }

  return (
    <>
     
  <form>
  <div className="mb-4">
    <label className="block text-white font-bold mb-2" for="title">
      Title
    </label>
    <input value={note.title}  required className="appearance-none bg-gray-800 rounded w-full py-2 px-3 text-white leading-tight focus:outline-none focus:shadow-outline" id="title" name='title' type="text" placeholder="Title" onChange={onChange}/>
  </div>
  <div className="mb-6">
    <label className="block text-white font-bold mb-2" for="description">
      Description
    </label>
    <textarea  required  name='description' className="appearance-none bg-gray-800 rounded w-full py-2 px-3 text-white leading-tight focus:outline-none focus:shadow-outline"  value={note.description} id="description" onChange={onChange} rows="4" placeholder="Enter your note here"></textarea>
  </div>
  <button  onClick={handleSubmit}  disabled={note.title.length<3 || note.description.length<5} className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-5 rounded focus:outline-none focus:shadow-outline" type="button">
    Add Note
  </button>
</form>
</>
  )
}

export default AddNote