import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import MetaData from './MetaData';


function MainScreen() {
 
  const [selectedSmoothie, setSelectedSmoothie] = useState('');
  const [quantity, setQuantity] = useState(1);
  const [productsAdded, setProductsAdded] = useState([]);
  const [totalProtein, setTotalProtein] = useState(0);
  const [totalCarbs, setTotalCarbs] = useState(0);
  const [totalCalories, setTotalCalories] = useState(0);


  const smoothieData = [
    { name: 'Almond Butter (1 tbsp)', calories: 95, carbs: 3, protein: 4 },
    { name: 'Almond Milk (1 cup)', calories: 60, carbs: 8, protein: 1 },
    { name: 'Almonds (1 Tsp) ', calories: 15, carbs: 0, protein: 1 },
    { name: 'Apple (1)', calories: 95, carbs: 25, protein: 0 },
    { name: 'Bananas (1)', calories: 105, carbs: 27, protein: 3 },
    { name: 'Cashew Butter (1 Tbsp)', calories: 95, carbs: 5, protein: 3 },
    { name: 'Cashew Milk ( 1 cup )', calories: 60, carbs: 9, protein: 1},
    { name: 'Chocolate Milk ( 1 cup ) ', calories: 209, carbs: 26, protein: 8},
    { name: 'Dark Chocolate , Ounce', calories: 155, carbs: 17, protein: 1 },
    { name: 'Coconut Milk ( 1 cup )', calories: 80, carbs: 7, protein: 1 },
    { name: 'Coffee (1 cup) Mixed', calories: 1, carbs: 0, protein: 0 },
    { name: 'Creatine ( Depend on Brand )', calories: 0, carbs: 0, protein: 0 },
    { name: 'Dates (1 Tbsp)', calories: 26, carbs: 7, protein: 0 },
    { name: 'Dragon fruit', calories: 61, carbs: 15, protein: 1 },
    { name: 'Egg Raw', calories: 71, carbs: 0, protein: 6 },
    { name: 'Peanut butter ( 1 Tpsp)', calories: 95, carbs: 3, protein: 4 },
    { name: 'Oats( 1 cup)', calories: 300, carbs: 27, protein:5},
    { name: 'Soya milk ( 1 cup)', calories: 110, carbs: 9, protein: 8},
    { name: 'Raisins - Small box', calories: 129, carbs: 34, protein: 1},
    { name: 'Milk - 2% ( 1 cup )', calories: 120, carbs: 12, protein: 8},
  ];

  const handleSmoothieChange = (event) => {
    setSelectedSmoothie(event.target.value);
  };

  const handleQuantityChange = (event) => {
    setQuantity(event.target.value);
  };

  const calculateTotalCalories = () => {
    const smoothie = smoothieData.find((s) => s.name === selectedSmoothie);
    if (smoothie) {
      return smoothie.calories * quantity;
    } else {
      return 0;
    }
  };

   const calculateTotalCarbs = () => {
    const smoothie = smoothieData.find((s) => s.name === selectedSmoothie);
    if (smoothie) {
      return smoothie.carbs * quantity;
    } else {
      return 0;
    }
  };


  const calculateTotalProtein = () => {
    const smoothie = smoothieData.find((s) => s.name === selectedSmoothie);
    if (smoothie) {
      return smoothie.protein * quantity;
    } else {
      return 0;
    }
  };


  const handleAddProduct = (event) => {
    event.preventDefault();
    const selectedSmoothieData = smoothieData.find(
      (smoothie) => smoothie.name === selectedSmoothie
    );
    const addedProduct = {
      name: selectedSmoothie,
      quantity: quantity,
      calories: selectedSmoothieData.calories * quantity,
      carbs: selectedSmoothieData.carbs * quantity,
      protein: selectedSmoothieData.protein * quantity,
    };
    setProductsAdded([
      ...productsAdded,
      addedProduct
    ]);
    setSelectedSmoothie('');
    setQuantity(1);
    setTotalProtein(totalProtein + addedProduct.protein)
    setTotalCarbs(totalCarbs + addedProduct.carbs )
    setTotalCalories(totalCalories + addedProduct.calories)
  };

  let totalPrice = 0;

  productsAdded.forEach(i => {
     return (totalPrice +++ i.calories)
  });

  return (
    <>
    <MetaData title={'JimBook - Track Progress'}/>
    <div className="bg-gradient-to-br from-gray-900 to-black pt-9">
      <div className="flex flex-col items-center justify-center min-h-screen pt-10">
      <i className=" text-white text-7xl fas fa-dumbbell mb-4"></i>
        <h1 className="text-white text-5xl font-bold mb-8">Jim Book</h1>
        <p className="text-white text-sm text-center mb-16">
          Now track your performance with calculating smoothie calories, and maintain consistency by noting your daily calories!
        </p>
        <Link to='/addNote' className="bg-orange-500 hover:bg-orange-700 text-white font-bold py-2 px-4 rounded">
    Get Started
  </Link>
      </div>


      {/* Smoothie Calculate */}
      <div className=" pt-10 min-h-screen flex flex-col justify-center items-center px-7 ">
      <h1 className="text-white text-center text-3xl font-bold my-6 pt-10">
        Smoothie Calculator
      </h1>
      <form className="w-100">
      <label className="block text-white font-bold my-4" htmlFor="smoothie">
      Select a smoothie Item:
    </label>
    <select
          className="w-100 border py-2 px-3 text-white rounded-lg bg-gray-700"

      id="smoothie"
      value={selectedSmoothie}
      onChange={handleSmoothieChange}
    >
      <option value="">Select a smoothie</option>
      {smoothieData.map((smoothie) => (
        <option key={smoothie.name} value={smoothie.name}>
          {smoothie.name}
        </option>
      ))}
    </select>

    <div className="mt-4">
      <label className="block text-white font-bold mb-2" htmlFor="quantity">
        Quantity:
      </label>
      <input
        className=" w-100 border py-2 px-3 text-white rounded-lg bg-gray-700"
        id="quantity"
        type="number"
        value={quantity}
        onChange={handleQuantityChange}
        min="1"
      />
    </div>

    <div className="flex justify-end mt-4">
      <button
        className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
        type="submit"
        onClick={handleAddProduct}
        disabled={!selectedSmoothie || quantity < 1}
      >
        Add to Calculate
      </button>
    </div>
  </form>


  <div className="mt-4 w-100 ">
     <h2 className="text-white text-2xl py-4 font-bold">Product Summary:</h2>
     <div className="text-white">
       <p className="mb-3 grid grid-cols-2 gap-4">Product: <span >  {selectedSmoothie} </span></p>
       <p className="mb-3 grid grid-cols-2 gap-4">Quantity:  <span >  {quantity} </span></p>
       <p className="mb-3 grid grid-cols-2 gap-4">Total Calories:  <span >  {calculateTotalCalories()} kcal </span> </p>
      <p className="mb-3 grid grid-cols-2 gap-4">Total Carbs: <span > {calculateTotalCarbs()} g </span></p>
        <p className="mb-3 grid grid-cols-2 gap-4">Total Protein: <span >  {calculateTotalProtein()} g </span></p>
      </div>
    </div>

  <div className="my-6 w-full ">
    <h2 className="text-white text-center font-bold text-3xl my-5">
      Products added:
    </h2>
   
    {productsAdded.length ? (
       <div className='overflow-x-auto whitespace-nowrap '>
      <table className="w-full text-left bg-gray-800 border-collapse overflow-x-auto whitespace-nowrap ">
        <thead>
          <tr>
            <th className="py-4 px-6 text-gray-200 uppercase tracking-wide">
              Name
            </th>
            <th className="py-4 px-6 text-gray-200 uppercase tracking-wide">
              Quantity
            </th>
            <th className="py-4 px-6 text-gray-200 uppercase tracking-wide">
              Calories
            </th>
            <th className="py-4 px-6 text-gray-200 uppercase tracking-wide">
              Carbs
            </th>
            <th className="py-4 px-6 text-gray-200 uppercase tracking-wide">
              Protein
            </th>
          </tr>
        </thead>
        <tbody>
          {productsAdded.map((product, index) => (
            <tr key={index}>
              <td className="py-4 px-6 border-gray-600 border-b text-white whitespace-no-wrap">
                {product.name}
              </td>
              <td className="py-4 px-6 border-gray-600 border-b text-green-500 whitespace-no-wrap">
                {product.quantity}
              </td>
              <td className="py-4 px-6 border-gray-600 border-b text-white whitespace-no-wrap">
                {product.calories} calories
              </td>
              <td className="py-4 px-6 border-gray-600 border-b text-sky-500 whitespace-no-wrap">
                {product.carbs} g
              </td>
              <td className="py-4 px-6 border-gray-600 border-b text-orange-400 whitespace-no-wrap">
                {product.protein} g
              </td>
              </tr>
      ))}
    </tbody>
    <tfoot>
      <tr>
        <td colSpan="2" className="py-4 px-6 border-gray-600 border-b text-gray-200 uppercase tracking-wide">
          Total
        </td>
        <td className="py-4 px-6 border-gray-600 border-b text-white whitespace-no-wrap">
          { totalCalories } calories
        </td>
        <td className="py-4 px-6 border-gray-600 border-b text-sky-500 whitespace-no-wrap">
          {totalCarbs} g
        </td>
        <td className="py-4 px-6 border-gray-600 border-b text-orange-400 whitespace-no-wrap">
          {totalProtein} g
        </td>
      </tr>
    </tfoot>
  </table>
  </div>
) : (
  <p className="text-white text-center ">No products added yet.</p>
)}
</div>


</div>
    </div>
    </>
  );
}

export default MainScreen;
