import React , {  useRef}from 'react';
import emailjs from '@emailjs/browser';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import MetaData from './MetaData';

const ContactPage = () => {

 
 //Email Js
  const form = useRef();

 const sendEmail = (e) => {
  e.preventDefault();

 

  emailjs.sendForm('service_9z67t1y', 'template_aahyiwo', form.current, '4LvVu58bpkcus2IVY')
  .then(() => {
      toast.success('Email sended Successfully!!')
    }, (error) => {
      toast.error(error)
    });
    e.target.reset();
  };
  

  return (
    <> <ToastContainer
    position="bottom-center"
    autoClose={3000}
    hideProgressBar={false}
    newestOnTop={false}
    closeOnClick
    rtl={false}
    pauseOnFocusLoss
    draggable
    pauseOnHover
    theme="dark"
    />
    <MetaData title={'Contact Us - JimBook'}/>
    <div className="bg-gradient-to-br from-gray-900 to-black min-h-screen flex flex-col items-center justify-center">

      <div className="w-1/2 p-8 rounded-lg shadow-lg bg-gray-900">
        <h1 className="text-4xl text-white font-bold mb-4">Contact Us</h1>
        <form className="flex flex-col" action="https://formbold.com/s/FORM_ID" ref={form} onSubmit={sendEmail}>
          <label htmlFor="name" className="text-white font-medium mb-2">Name</label>
          <input type="text" id="name" name="name" className="bg-gray-900 rounded-lg p-2 mb-4 text-white" required />

          <label htmlFor="email" className="text-white font-medium mb-2">Email</label>
          <input type="email" id="email" name="email" className="bg-gray-900 rounded-lg p-2 mb-4 text-white" required />

          <label htmlFor="message" className="text-white font-medium mb-2">Message</label>
          <textarea id="message" name="message" className="bg-gray-900 rounded-lg p-2 mb-4 h-32 text-white" required></textarea>

          <button type="submit" className="bg-green-500 hover:bg-green-600 rounded-lg text-white font-medium py-2 px-4">Submit</button>
        </form>
      </div>
    </div>
    </>
  );
};

export default ContactPage;
