import React, {useContext } from 'react'
import noteContext from '../context/notes/noteContext'

const NoteItem = (props) => {
  const { note , updateNote } = props;

  
  const context = useContext(noteContext)
  const { deleteNote } = context;

  
  

  return (

    <div className="bg-gray-800 rounded-lg shadow-lg p-4 my-3" >
    <h2 className="text-2xl font-normal py-3 text-white mb-2">{note.title}</h2>
    <p className="text-gray-300 text-base mb-4">{note.description}</p>
    <div className="flex justify-between items-center">
      <p className="text-gray-500 text-sm">Created at: <span className="font-bold text-white px-3">{note.date}</span></p>
      <div className="flex">
        <button className="bg-orange-500 hover:bg-gray-600 rounded-full h-8 w-8 flex items-center justify-center mr-2 focus:outline-none" onClick={()=>{updateNote(note)}}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" className="w-4 h-4">
            <path d="M18.432,4.565l-2.798-2.798c-0.481-0.481-1.128-0.748-1.802-0.748s-1.322,0.267-1.802,0.748l-1.23,1.23l5.4,5.4 l1.23-1.23C19.213,8.166,19.213,5.897,18.432,4.565z"/>
            <path d="M13.12,7.348L2.869,17.6c-0.973,0.973-0.973,2.556,0,3.529c0.973,0.973,2.556,0.973,3.529,0l10.251-10.251V7.348z"/>
          </svg>
        </button>
        <button className="bg-red-500 hover:bg-red-600 rounded-full h-8 w-8 flex items-center justify-center focus:outline-none" onClick={() =>{ deleteNote(note._id)}}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" className="w-4 h-4">
            <path d="M6.392,6.392c0.39-0.39,1.023-0.39,1.414,0l2.828,2.828l2.828-2.828c0.39-0.39,1.023-0.39,1.414,0 c0.39,0.39,0.39,1.023,0,1.414l-2.828,2.828l2.828,2.828c0.39,0.39,0.39,1.023,0,1.414c-0.39,0.39-1.023,0.39-1.414,0 l-2.828-2.828l-2.828,2.828c-0.39,0.39-1.023,0.39-1.414,0c-0.39-0.39-0.39-1.023,0-1.414l2.828-2.828L6.392,6.392z"/>
          </svg>
        </button>
      </div>
    </div>
  </div>
  

  );
  
};

export default NoteItem;
