import React, {useState , useEffect} from 'react'
import { useHistory } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loader from './Loader';
import MetaData from './MetaData';

const Login = () => {
    let history = useHistory()
    const [cred, setCred] = useState({email: "" , password : "" })
    //Login API Call

    let loading = null;

    const handleSubmit = async (e ) => {
        e.preventDefault()

        loading = true ;
        const response = await fetch(`/api/auth/loginUser`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({email : cred.email, password : cred.password})
          });
          const json = await response.json();

          if (json.success) {
            //Saving auth-token and redirecting using history hook
            localStorage.setItem('token' , json.authToken)
            loading = false;
            toast.success('You are logged in')
            history.push('/')
          }
          else {
            toast.error(json.error)
            loading = false;
          }
    }

    useEffect(() => {
      if(localStorage.getItem('token')) {
        history.push('/')
      }
    }, [])
    

    const pushRegister = () => {
      history.push('/signUp')
    }

    const onchange = (e)=>{
        setCred({...cred, [e.target.name]: e.target.value})
    }


  return (
      <>
         <ToastContainer
      position="bottom-center"
      autoClose={3000}
      hideProgressBar={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
      theme="dark"
      />
      <MetaData title={'Login - JimBook'}/>
          {loading ? <Loader/>  : (
             <>
          
      <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 flex justify-center items-center px-4 sm:px-6 lg:px-8">
          <div className="max-w-md w-full space-y-8">
            <div>
              <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-100">
                Login 
              </h2>
            </div>
            <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
              <input type="hidden" name="remember" value="true"/>
              <div className="rounded-md shadow-sm -space-y-px">
                <div>
                  <label for="email-address" className="sr-only">Email address</label>
                  <input onChange={onchange} value={cred.email} aria-describedby="emailHelp" name='email' id="email-address" type="email" autocomplete="email" required className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-white rounded-t-md focus:outline-none focus:ring-primary-500 focus:border-primary-500 focus:z-8 sm:text-sm bg-gray-800 text-gray-100" placeholder="Email address"/>
                </div>
                <div>
                  <label for="password" className="sr-only">Password</label>
                  <input  onChange={onchange} value={cred.password}  name='password' id="password"  type="password" autocomplete="current-password" required className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-white focus:outline-none focus:ring-primary-500 focus:border-primary-500 focus:z-8 sm:text-sm bg-gray-800 text-gray-100" placeholder="Password"/>
                </div>
              </div>
        
              <div className="flex items-center justify-between">
                <div className="flex items-center">
                  <input required id="remember_me" name="remember_me" type="checkbox" className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"/>
                  <label  for="remember_me" className="ml-2 block text-sm text-gray-100">
                    Remember me
                  </label>
                </div>
        
          
              </div>
        
              <div>
              <button type="submit" className="loginbtn group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md bg-primary-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500">
        Sign in
      </button>
      <button onClick={pushRegister} className=" mt-4 group text-gray-800 relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md bg-white focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500">
       Don't Have Account
      </button>
            </div>
          </form>
        </div>
      </div>
        
          </>
          )}
      </>
  )
}

export default Login