import React, {useState , useEffect} from 'react'
import { useHistory } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loader from './Loader';
import MetaData from './MetaData';


const SignUp = () => {
    let history = useHistory()
    const [cred, setCred] = useState({ name : "" , email: "" , password : "" })
  
    let loading = null;
    const handleSubmit = async  (e) => {
        e.preventDefault()
        // const { name , email,  password} = cred;
         loading = true
        const response = await fetch(`/api/auth/createUser`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({name : cred.name , email : cred.email , password : cred.password})
          });
          const json = await response.json();
          
          

          if (json.success) {
            localStorage.setItem('token' , json.authToken)
            loading = false
            history.push('/')
            toast.success('You are successfully Registered')
          }
          else {
            toast.error(json.error)
            loading=  false
            history.push("/login")
          }
    }

    useEffect(() => {
      if(localStorage.getItem('token')) {
        history.push('/')
      }
    }, [])

    const onchange = (e)=>{
        setCred({...cred, [e.target.name]: e.target.value})
    }

  return (
 
    <>
    <MetaData title={'Register - JimBook'}/>
     <ToastContainer
        position="bottom-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
        />
    {loading ? <Loader/> : (
        <>
            <div className=" min-h-screen  bg-gradient-to-r from-gray-800 to-black text-white py-10 px-4 sm:px-6 lg:px-8">
            <div className="max-w-md mx-auto justify-center py-10 ">
              <h2 className="text-2xl font-bold mb-4 pt-10 text-center">Sign up</h2>
              <form onSubmit={handleSubmit}>
                <div className="mb-4">
                  {/* <label className="block font-semibold mb-2">Name</label> */}
                  <input name='name' id='name' onChange={onchange} required type="name" className="w-full p-2 rounded-lg bg-gray-800 border-none focus:outline-none focus:bg-gray-700" placeholder="Arnold/ Your" value={cred.name}/>
                </div>
                <div className="mb-4">
                  {/* <label className="block font-semibold mb-2">Email</label> */}
                  <input name='email' id='email' value={cred.email} required  onChange={onchange} type="email" className="w-full p-2 rounded-lg bg-gray-800 border-none focus:outline-none focus:bg-gray-700" placeholder="jim@example.com"/>
                </div>
                <div className="mb-4">
                  {/* <label className="block font-semibold mb-2">Password</label> */}
                  <input name='password' id='password' value={cred.password} required   onChange={onchange} type="password" className="w-full p-2 rounded-lg bg-gray-800 border-none focus:outline-none focus:bg-gray-700" placeholder="********"/>
                </div>
                <div className="mb-4">
                  <input required type="checkbox" className="mr-2 leading-tight" id="checkbox"/>
                  <label for="checkbox" className="font-semibold text-sm">Check me out</label>
                </div>
                <button type="submit" className="w-full py-2 px-4 bg-indigo-500 text-white rounded-lg font-semibold hover:bg-indigo-600 transition duration-200">Sign up</button>
              </form>
            </div>
          </div>
          </>
    )}
    </>

  )
}

export default SignUp